.content-home > * {
  margin-bottom: 1em;
}

.header-home {
  text-align: center;
  margin-bottom: 0.5em;
}

.screenshot-input-selection {
  text-align: center;
}

.screenshot-input-selection-button {
  margin: 0.2em;
}

.screenshot-input-file-wrapper {
  outline: 3px dashed;
  height: 11em;
}

.screenshot-input-file {
  width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}

.screenshot-input-file-label-wrapper {
  width: 100%;
  height: 100%;
  display: table;
  text-align: center;
}
.screenshot-input-file-label {
  display: table-cell;
  vertical-align: middle;
}

.choose-file-link {
  color: blue;
  text-decoration: underline;
}

.screenshot-input-url-wrapper {
  text-align: center;
  height: 11em;
}

.screenshot-input-url {
  width: 85%;
}

.search-result-wrapper {
  text-align: center;
}

.screenshot-search-button {
  margin-top: 0.5em;
}

.popular-tweets-wrapper {
  text-align: center;
}

.centered-block{
  display: inline-block;
  padding: 1%;
  width: 100%;
}

.tweet-redirect{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}

.twitter-tweet{
  margin-left: auto;
  margin-right: auto;
}

