.article {
  border: 1px solid #dee2e6;
  border-radius: 1em;
  padding: 1em;
}

.articles > .article {
  margin-bottom: 1em;
}

.article-image {
  float: left;
  margin-right: 1em;
  margin-bottom: 0.5em;
}

.article-title {
  font-weight: bold;
}

.article-author {
  font-style: italic;
  color: grey;
  float: left;
}

.article-published-at {
  color: grey;
  float: left;
}

.comment {
  margin-bottom: 1em;
}

.comment-username {
  font-weight: bold;
}

.comment-date {
  color: gray;
  font-style: italic;
}

.comment-profile-picture {
  float: left;
  width: 4em;
  height: 4em;
  margin-bottom: 1em;
  margin-right: 1em;
}

.tweet-embed {
  display: inline-block;
  max-width: 100%;
}

.show-comments-button-label {
  text-transform: none;
  font-weight: bold;
}